import { FC } from "react";

import styles from './advantages.module.css';
import layout from '../../../layout/layout.module.css'

const Advantages: FC = () => {
    
    return <>
        <h1 className={layout.cardLabel}>    </h1>
        <div className={styles.fgrid}>
            <div className={styles.fbig}>   <br /> <b className={styles.purple}></b>     </div>
            <div className={styles.fsecbig}>
                <div className={styles.fmid}> <b className={styles.purple}></b>,   <br />     Aroma Box</div>
                <div className={styles.smWrapper}>
                    <div className={styles.fsm1}><b className={styles.purple}></b>        </div>
                    <div className={styles.fsm2}><b className={styles.purple}></b>  <br />       </div>
                </div>
            </div>
        </div>
    </>
}

export default Advantages;